@import 'styles/fonts.scss';
@import 'styles/colors.scss';
@import 'styles/spacing.scss';
@import 'styles/sizing.scss';

.Form {
	display: flex;
	justify-content: center;
	align-items: center;

	flex-direction: column;

	.Input {
		margin-bottom: $md-space;
	}

	.Error {
		font-family: $primary-font;
		font-size: $sm-size;

		color: $danger-color;

		margin-top: $md-space;
	}

	.Button {
		margin-top: $xxl-space;
	}
}

.Actions {
	display: flex;
	justify-content: center;
	align-items: center;

	flex-direction: column;

	margin-bottom: $md-space;

	button {
		&:first-child {
			margin-bottom: $sm-space;
		}
	}

	.Separation {
		display: none;

		width: 1px;
		height: $lg-size;

		background-color: $third-color;
	}
}

@media (min-width: 720px) {
	.Heading {
		padding-top: 120px;

		.Name {
			font-size: 5em;
		}

		.Description {
			font-size: $md-size;

			margin-top: $md-space;
		}
	}

	.Actions {
		flex-direction: row;

		button {
			&:first-child {
				margin-bottom: 0;
			}
		}

		.Separation {
			display: block;

			margin: 0 $xl-space;
		}
	}
}

@media (min-width: 1025px) {
	.Form,
	.Actions {
		margin-left: 7rem;
	}

	.Form {
		align-items: flex-start;
	}
}
